/**
 * mescroll-more-itemmixins,  mescroll-body  ( mescroll-more )
 */
const MescrollMoreItemMixin = {
	// propsmixin,

	props:{
		i: Number, // tab
		index: { // tab
			type: Number,
			default(){
				return 0
			}
		}
	},

	data() {
		return {
			downOption:{
				auto:false // 
			},
			upOption:{
				auto:false // 
			},
			isInit: false // tab
		}
	},
	watch:{
		// 
		index(val){
			if (this.i === val && !this.isInit) {
				this.isInit = true; // true
				// this.mescroll && this.mescroll.triggerDownScroll();
				this.mescroll && this.mescroll.resetUpScroll();
			}
		}
	},
	methods: {
		// refmescroll ()
		mescrollInitByRef() {
			if(!this.mescroll || !this.mescroll.resetUpScroll){
				// ref, mescrollref, 'mescrollRef'
				let mescrollRef = this.$refs.mescrollRef || this.$refs['mescrollRef'+this.i];
				if(mescrollRef) this.mescroll = mescrollRef.mescroll
			}
		},
		// mescroll,mescroll (mescroll-mixins.jsmescrollInit, isInit)
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
			this.mescrollInitByRef && this.mescrollInitByRef(); // 
			// tab
			if(this.i === this.index){
				this.isInit = true; // true
				this.mescroll.resetUpScroll();
				// this.mescroll.triggerDownScroll();
			}
		},
	}
}

export default MescrollMoreItemMixin;
